import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NewsService } from '../news.service';
@Component({
  selector: 'app-news-category',
  templateUrl: './news-category.component.html',
  styleUrls: ['./news-category.component.css']
})
export class NewsCategoryComponent implements OnInit {
  newsData: any;
  category: any;
  constructor(private newsService:  NewsService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => { this.category = params['category']; });
    this.getNewsCategory(this.category );
  }
  getNewsCategory(category: any) {
    const that = this;
    this.newsService.getNewsByCategory(category).subscribe(
      res => { that.newsData = res.data;
         console.log(that.newsData);
         },
      error => { console.log(error); },
      () => { console.log('Its over News.'); }
      );
  }
}



