import {Routes, RouterModule} from '@angular/router';
import { NewsShowMainComponent } from './news-show-main/news-show-main.component';
import { NewsShowDetailComponent } from './news-show-detail/news-show-detail.component';
import { NewsSearchComponent } from './news-search/news-search.component';
import { NewsCategoryComponent } from './news-category/news-category.component';
import { PollCreateComponent } from './poll-create/poll-create.component';
import { PollShowComponent } from './poll-show/poll-show.component';
import { NewsAdminComponent } from './news-admin/news-admin.component';
import { NewsAdvtComponent } from './news-advt/news-advt.component';
const routes: Routes = [
{path: '', component: NewsShowMainComponent},
{path: 'news-detail/:id', component: NewsShowDetailComponent},
{path: 'news-category/:category', component: NewsCategoryComponent},
{path: 'news-create', component: NewsSearchComponent},
{path: 'poll-create', component: PollCreateComponent},
{path: 'poll-show', component: PollShowComponent},
{path: 'advt-create', component: NewsAdvtComponent},
{path: 'news-admin', component: NewsAdminComponent},
];
export const appRouterModule = RouterModule.forRoot(routes, {enableTracing: false});




