import {  Component, OnInit  } from '@angular/core';
import { NewsadvtService } from '../newsadvt.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-news-advt',
  templateUrl: './news-advt.component.html',
  styleUrls: ['./news-advt.component.css']
})

export class NewsAdvtComponent implements OnInit  {

id:  number;
todelete:  number;
updateflag:  boolean;
addflag:  boolean;
loader:  boolean;
pageno:  number  =  1;
totalrows:  number;
totalpages:  number;
rowsperpage:  number  =  5;
strat:  number;
end:  number;
message:  string;
Advt: any;
AdvtDetails: any;
AdvtCategories: any;
successFlag: boolean;
constructor(private newsadvtService:  NewsadvtService)  {
  this.updateflag = false;
  this.addflag = false;
  this.loader = false;
  this.successFlag = false;
  this.setIndex(1);
 }

// set  pageno(val: number) {
// if(val! =  = this.pageno) {
// this._pageno = val; }
// console.log(this._pageno) }

  first() { this.setIndex(this.pageno = 1); }
  previous() { this.setIndex(this.pageno = this.pageno - 1); }
  next() { this.setIndex(this.pageno = this.pageno + 1); }
  last() { this.setIndex(this.pageno = this.totalpages); }
  setIndex(page: number): void {
    this.strat = ((page - 1) * this.rowsperpage) + 1;
    this.end = page * this.rowsperpage;
   }
createAdvt() {
  this.addflag = true;
  this.updateflag = false;
  this.successFlag = false;
  this.resetAdvt();
 }
closeAdvt() {
  this.resetAdvt();
 this.addflag = false;
 this.updateflag = false;
 this.getAdvtDetails();
 }
  ngOnInit() {
    this.AdvtDetails = [];
    this.getAdvtDetails();
    this.Advt = {'header': '', 'description': '', 'imageUrl': ''};
   }



  getAdvtDetails() {
  const that = this;
  this.loader = true;
  this.newsadvtService.getAdvt().subscribe(
    res => { that.AdvtDetails = res.data; console.log(that.AdvtDetails);
      that.totalpages = Math.ceil(that.totalrows / that.rowsperpage);
      that.loader = false;
      // this.addflag = false;
    console.log(that.totalpages); },
    error => { console.log(error); },
    () => { console.log('Its over Advt.'); }
    );
}

   getAdvtDetailsById(id: number) {
     this.addflag = true;
     this.updateflag = true;
     this.loader = true;
     this.successFlag = false;
  const that = this;
  this.newsadvtService.getAdvtById(id).subscribe(
    res => {
       that.id = res.data._id;
      that.Advt = res.data;
       that.loader = false;
      console.log(res); },
    error => { console.log(error); },
    () => { console.log('Its over Advt.'); }
    );
   }

   addAdvt(Advtform: NgForm): void {
    if (Advtform.valid) {
  const AdvtBody = Advtform.value;
  AdvtBody.option1Count = 0;
  AdvtBody.option2Count = 0;
  AdvtBody.option3Count = 0;
  AdvtBody.option4Count = 0;
  this.newsadvtService.addAdvt(AdvtBody).subscribe(
  res => {
    console.log(res);
    this.message = 'Advt created successfully.';
   // this.getAdvtDetails();
    this.successFlag = true;
   // this.addflag = false;
  },
  error => {console.log(error); },
    () => {console.log('Its over add Advt.'); }
    );
  }
}
deleteAdvt(id: number): void {
  const that = this;
  const r = confirm('Do you want to delete the Advt?');
if (r === true) {
     this.newsadvtService.deleteAdvt(id).subscribe(
  res => {console.log(res);
    this.message = 'Advt deleted successfully.';
    that.getAdvtDetails();
   // $("#myModal").modal('show');
 },
  error => {console.log(error); },
    () => {console.log('Advt deleted.'); }
    );
}
}
updateAdvt(Advtform: NgForm): void {
  this.loader = true;
  console.log(this.id);
  if (Advtform.valid) {
    const AdvtBody = Advtform.value;
  this.newsadvtService.updateAdvt(AdvtBody, this.id).subscribe(
  res => { console.log(res);
   // this.resetAdvt();
   // this.getAdvtDetails();
    this.updateflag = false;
    this.successFlag = true;
    // this.addflag = false;
    this.loader = false;
    document.getElementById('messagebody').innerHTML = 'Advt updated successfully.';
    this.message = 'Advt updated successfully.';
      },
   error => { console.log(error); },
     () => { console.log('updated Advt.'); });
    }
 }


resetAdvt(): void {
  this.Advt = {'header': '', 'description': '', 'imageUrl': ''};
 }



 }





