import {  Component, OnInit  } from '@angular/core';
import { PollService } from '../poll.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-poll-create',
  templateUrl: './poll-create.component.html',
  styleUrls: ['./poll-create.component.css']
})


export class PollCreateComponent implements OnInit  {

id:  number;
todelete:  number;
updateflag:  boolean;
addflag:  boolean;
loader:  boolean;
pageno:  number  =  1;
totalrows:  number;
totalpages:  number;
rowsperpage:  number  =  5;
strat:  number;
end:  number;
message:  string;
Poll: any;
PollDetails: any;
PollCategories: any;
successFlag: boolean;
constructor(private pollService:  PollService)  {
  this.updateflag = false;
  this.addflag = false;
  this.loader = false;
  this.successFlag = false;
  this.setIndex(1);
 }

// set  pageno(val: number) {
// if(val! =  = this.pageno) {
// this._pageno = val; }
// console.log(this._pageno) }

  first() { this.setIndex(this.pageno = 1); }
  previous() { this.setIndex(this.pageno = this.pageno - 1); }
  next() { this.setIndex(this.pageno = this.pageno + 1); }
  last() { this.setIndex(this.pageno = this.totalpages); }
  setIndex(page: number): void {
    this.strat = ((page - 1) * this.rowsperpage) + 1;
    this.end = page * this.rowsperpage;
   }
createPoll() {
  this.addflag = true;
  this.updateflag = false;
  this.successFlag = false;
  this.resetPoll();
 }
closePoll() {
  this.resetPoll();
 this.addflag = false;
 this.updateflag = false;
 this.getPollDetails();
 }
  ngOnInit() {
    this.PollDetails = [];
    this.getPollDetails();
    this.Poll = {'question': '', 'option1': '', 'option2': '', 'option3': '', 'option4': ''};
   }



  getPollDetails() {
  const that = this;
  this.loader = true;
  this.pollService.getPoll().subscribe(
    res => { that.PollDetails = res.data; console.log(that.PollDetails);
      that.totalpages = Math.ceil(that.totalrows / that.rowsperpage);
      that.loader = false;
      // this.addflag = false;
    console.log(that.totalpages); },
    error => { console.log(error); },
    () => { console.log('Its over Poll.'); }
    );
}

   getPollDetailsById(id: number) {
     this.addflag = true;
     this.updateflag = true;
     this.loader = true;
     this.successFlag = false;
  const that = this;
  this.pollService.getPollById(id).subscribe(
    res => {
       that.id = res.data._id;
      that.Poll = res.data;
       that.loader = false;
      console.log(res); },
    error => { console.log(error); },
    () => { console.log('Its over Poll.'); }
    );
   }

   addPoll(Pollform: NgForm): void {
    if (Pollform.valid) {
  const PollBody = Pollform.value;
  PollBody.option1Count = 0;
  PollBody.option2Count = 0;
  PollBody.option3Count = 0;
  PollBody.option4Count = 0;
  this.pollService.addPoll(PollBody).subscribe(
  res => {
    console.log(res);
    this.message = 'Poll created successfully.';
   // this.getPollDetails();
    this.successFlag = true;
   // this.addflag = false;
  },
  error => {console.log(error); },
    () => {console.log('Its over add Poll.'); }
    );
  }
}
deletePoll(id: number): void {
  const that = this;
  const r = confirm('Do you want to delete the Poll?');
if (r === true) {
     this.pollService.deletePoll(id).subscribe(
  res => {console.log(res);
    this.message = 'Poll deleted successfully.';
    that.getPollDetails();
   // $("#myModal").modal('show');
 },
  error => {console.log(error); },
    () => {console.log('Poll deleted.'); }
    );
}
}
updatePoll(Pollform: NgForm): void {
  this.loader = true;
  console.log(this.id);
  if (Pollform.valid) {
    const PollBody = Pollform.value;
  this.pollService.updatePoll(PollBody, this.id).subscribe(
  res => { console.log(res);
   // this.resetPoll();
   // this.getPollDetails();
    this.updateflag = false;
    this.successFlag = true;
    // this.addflag = false;
    this.loader = false;
    document.getElementById('messagebody').innerHTML = 'Poll updated successfully.';
    this.message = 'Poll updated successfully.';
      },
   error => { console.log(error); },
     () => { console.log('updated Poll.'); });
    }
 }


resetPoll(): void {
  this.Poll = {'question': '', 'option1': '', 'option2': '', 'option3': '', 'option4': ''};
 }



 }




