import {  Component, OnInit  } from '@angular/core';
import { NewsService } from '../news.service';
import { NgForm } from '@angular/forms';
@Component( {
  selector:  'app-news-search',
  templateUrl:  './news-search.component.html',
  styleUrls:  ['./news-search.component.css']
 })
export class NewsSearchComponent implements OnInit  {

id:  number;
name:  string;
email:  string;
age:  number;
password:  string;
todelete:  number;
updateflag:  boolean;
addflag:  boolean;
loader:  boolean;
pageno:  number  =  1;
totalrows:  number;
totalpages:  number;
rowsperpage:  number  =  5;
strat:  number;
end:  number;
message:  string;
news: any;
newsDetails: any;
newsCategories: any;
successFlag: boolean;
constructor(private newsService:  NewsService)  {
  this.updateflag = false;
  this.addflag = false;
  this.loader = false;
  this.successFlag = false;
  this.setIndex(1);
 }

// set  pageno(val: number) {
// if(val! =  = this.pageno) {
// this._pageno = val; }
// console.log(this._pageno) }

  first() { this.setIndex(this.pageno = 1); }
  previous() { this.setIndex(this.pageno = this.pageno - 1); }
  next() { this.setIndex(this.pageno = this.pageno + 1); }
  last() { this.setIndex(this.pageno = this.totalpages); }
  setIndex(page: number): void {
    this.strat = ((page - 1) * this.rowsperpage) + 1;
    this.end = page * this.rowsperpage;
   }
createNews() {
  this.addflag = true;
  this.updateflag = false;
  this.successFlag = false;
  this.resetNews();
 }
closeNews() {
  this.resetNews();
 this.addflag = false;
 this.updateflag = false;
 this.getNewsDetails();
 }
  ngOnInit()  {
    this.getNewsDetails();
    this.news = {'headline': '', 'description': '', 'imageUrl': '', 'videoUrl': '', 'category': '',
    'filetoupload': '', 'active': ''};
     this.getNewsCategory();
   }

   getNewsCategory() {
    const that = this;
    this.loader = true;
    this.newsService.getNewsCategory().subscribe(
      res => { that.newsCategories = res.data; },
      error => { console.log(error); },
      () => { console.log('Its over News.'); }
      );
  }

  getNewsDetails() {
  const that = this;
  this.loader = true;
  this.newsService.getNews().subscribe(
    res => { that.newsDetails = res.data; console.log(that.newsDetails);
      that.totalpages = Math.ceil(that.totalrows / that.rowsperpage);
      that.loader = false;
      // this.addflag = false;
    console.log(that.totalpages); },
    error => { console.log(error); },
    () => { console.log('Its over News.'); }
    );
}

   getNewsDetailsById(id: number) {
     this.addflag = true;
     this.updateflag = true;
     this.loader = true;
     this.successFlag = false;
  const that = this;
  this.newsService.getNewsById(id).subscribe(
    res => {
       that.id = res.data._id;
      that.news = res.data;
       that.loader = false;
      console.log(res); },
    error => { console.log(error); },
    () => { console.log('Its over News.'); }
    );
   }

   addNews(newsform: NgForm): void {
    if (newsform.valid) {
  const newsBody = newsform.value;
  this.newsService.addNews(newsBody).subscribe(
  res => {
    console.log(res);
    this.message = 'News created successfully.';
   // this.getNewsDetails();
    this.successFlag = true;
   // this.addflag = false;
  },
  error => {console.log(error); },
    () => {console.log('Its over add news.'); }
    );
  }
}
deleteNews(id: number): void {
  const r = confirm('Do you want to delete the news?');
if (r === true) {
     this.newsService.deleteNews(id).subscribe(
  res => {console.log(res);
    this.message = 'News deleted successfully.';
    this.getNewsDetails();
   // $("#myModal").modal('show');
 },
  error => {console.log(error); },
    () => {console.log('News deleted.'); }
    );
}
}
updateNews(newsform: NgForm): void {
  this.loader = true;
  console.log(this.id);
  if (newsform.valid) {
    const newsBody = newsform.value;
  this.newsService.updateNews(newsBody, this.id).subscribe(
  res => { console.log(res);
   // this.resetNews();
   // this.getNewsDetails();
    this.updateflag = false;
    this.successFlag = true;
    // this.addflag = false;
    this.loader = false;
    document.getElementById('messagebody').innerHTML = 'News updated successfully.';
    this.message = 'News updated successfully.';
      },
   error => { console.log(error); },
     () => { console.log('updated News.'); });
    }
 }


resetNews(): void {
  this.news = {'headline': '', 'description': '', 'imageUrl': '', 'videoUrl': '', 'category': ''};

 }



 }



