import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NewsService } from '../news.service';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
@Component({
  selector: 'app-news-show-detail',
  templateUrl: './news-show-detail.component.html',
  styleUrls: ['./news-show-detail.component.css'],
})
export class NewsShowDetailComponent implements OnInit {
  id: any;
  news: any;
  newsId: any;
  youtubepath: SafeResourceUrl;
  constructor(private newsService: NewsService, private route: ActivatedRoute,
     private sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.route.params.subscribe(params => { this.newsId = params['id']; });
    this.getNewsDetailsById(this.newsId );
    // this.news.videoUrl = 'https://www.youtube.com/embed/8qF925ZrovM';
  }
  getNewsDetailsById(id: any) {
 const that = this;
 this.newsService.getNewsById(id).subscribe(
   res => {
     that.id = res.data._id;
     that.news = res.data;
     // that.news.videoUrl = 'https://www.youtube.com/embed/' + that.news.videoUrl;
     that.youtubepath = that.sanitizer.bypassSecurityTrustResourceUrl('https://www.youtube.com/embed/' + that.news.videoUrl);
    console.log(that.youtubepath); },
   error => { console.log(error); },
   () => { console.log('Its over News.'); }
   );
  }

}



