import { Component, OnInit } from '@angular/core';
import { NewsService } from '../news.service';
import { PollService } from '../poll.service';
import { NewsadvtService } from '../newsadvt.service';
@Component({
  selector: 'app-news-show-main',
  templateUrl: './news-show-main.component.html',
  styleUrls: ['./news-show-main.component.css']
})
export class NewsShowMainComponent implements OnInit {

  newsData: any;
  PollDetails: any;
  todayPoll: any;
  VoteId: any;
  category1: any = '';
  category1Array: any;
  category2: any = '';
  category2Array: any;
  category3: any = '/';
  category3Array: any;
  AdvtDetails: any;
  constructor(private newsService:  NewsService, private pollService: PollService,
  private newsadvtService: NewsadvtService) { }

  ngOnInit() {
    this.category1Array = [];
    this.category2Array = [];
    this.category3Array = [];
  this.getNewsDetails();
this.getPollDetails();
this.VoteId = localStorage.getItem('pollVote');
  }
  getNewsDetails() {
    const that = this;
    this.newsService.getNews().subscribe(
      res => { that.newsData = res.data;
         console.log(that.newsData);
         (that.newsData).forEach(newscat => {
            if (newscat.category === that.category1) {
                that.category1Array.push(newscat);
            } else if (newscat.category === that.category2) {
              that.category2Array.push(newscat);
            } else if (newscat.category === that.category3) {
              that.category3Array.push(newscat);
            }
            });
         },
      error => { console.log(error); },
      () => { console.log('Its over News.'); }
      );
  }
  getPollDetails() {
    const that = this;
    this.pollService.getPoll().subscribe(
      res => { that.PollDetails = res.data; },
      error => { console.log(error); },
      () => { console.log('Its over Poll.'); }
      );
  }
  getAdvtDetails() {
    const that = this;
    this.newsadvtService.getAdvt().subscribe(
      res => { that.AdvtDetails = res.data; console.log(that.AdvtDetails);
        },
      error => { console.log(error); },
      () => { console.log('Its over Advt.'); }
      );
  }

  updatePoll(pollId: any): void {
    console.log(this.todayPoll);
    localStorage.setItem('pollVote', pollId);
    this.VoteId = pollId;
    this.pollService.updatePollCount({'updateField': this.todayPoll}, pollId).subscribe(
    res => { console.log(res);
        },
     error => { console.log(error); },
       () => { console.log('updated Poll.'); });
      }
}



