import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'pagination'
})
export class PaginationPipe implements PipeTransform {

  transform(param: any, start?: any, end?: any): any {
    const value = param.slice(start - 1, end);
    return value;
  }

}



