import { Component, OnInit } from '@angular/core';
import { PollService } from '../poll.service';
@Component({
  selector: 'app-poll-show',
  templateUrl: './poll-show.component.html',
  styleUrls: ['./poll-show.component.css']
})
export class PollShowComponent implements OnInit {

  PollDetails: any;
  todayPoll: any;
  VoteId: any;
  constructor(private pollService: PollService) { }

  ngOnInit() {
    this.getPollDetails();
    this.VoteId = localStorage.getItem('pollVote');

  }

  getPollDetails() {
    const that = this;
    this.pollService.getPoll().subscribe(
      res => { that.PollDetails = res.data; },
      error => { console.log(error); },
      () => { console.log('Its over Poll.'); }
      );
  }

  updatePoll(pollId: any): void {
    console.log(this.todayPoll);
    localStorage.setItem('pollVote', pollId);
    this.VoteId = pollId;
    this.pollService.updatePollCount({'updateField': this.todayPoll}, pollId).subscribe(
    res => { console.log(res);
        },
     error => { console.log(error); },
       () => { console.log('updated Poll.'); });
      }

}



